import { TaskCreationWizard } from '../components/TaskCreationWizard';

// API helper
const API_BASE = '/api';

async function authFetch(endpoint: string, options: RequestInit = {}): Promise<Response> {
  const stored = localStorage.getItem('auth-storage');
  let token: string | null = null;
  if (stored) {
    const parsed = JSON.parse(stored);
    token = parsed.state?.accessToken || null;
  }

  const headers: HeadersInit = {
    'Content-Type': 'application/json',
    ...options.headers,
  };

  if (token) {
    (headers as Record<string, string>)['Authorization'] = `Bearer ${token}`;
  }

  return fetch(`${API_BASE}${endpoint}`, {
    ...options,
    headers,
  });
}

// View types for sidebar navigation
type SidebarView =
  | 'kanban'
  | 'terminals'
  | 'insights'
  | 'roadmap'
  | 'ideation'
  | 'changelog'
  | 'context'
  | 'github-issues'
  | 'worktrees'
  | 'agent-profiles'
  | 'settings';

// Settings tab type
type SettingsTab = 'agent-profile' | 'credentials';

interface NavItem {
  id: SidebarView;
  label: string;
  icon: React.ElementType;
  shortcut?: string;
}

const projectNavItems: NavItem[] = [
  { id: 'kanban', label: 'Kanban Board', icon: LayoutGrid, shortcut: 'K' },
  { id: 'terminals', label: 'Agent Terminals', icon: Terminal, shortcut: 'A' },
  { id: 'insights', label: 'Insights', icon: Sparkles, shortcut: 'N' },
  { id: 'roadmap', label: 'Roadmap', icon: Map, shortcut: 'D' },
  { id: 'ideation', label: 'Ideation', icon: Lightbulb, shortcut: 'I' },
  { id: 'changelog', label: 'Changelog', icon: FileText, shortcut: 'L' },
  { id: 'context', label: 'Context', icon: BookOpen, shortcut: 'C' },
];

const toolsNavItems: NavItem[] = [
  { id: 'github-issues', label: 'GitHub Issues', icon: Github, shortcut: 'G' },
  { id: 'worktrees', label: 'Worktrees', icon: GitBranch, shortcut: 'W' },
  { id: 'agent-profiles', label: 'Agent Profiles', icon: UserCog, shortcut: 'P' },
];

// Project type matching backend
interface Project {
  id: string;
  name: string;
  path: string;
  repo_url: string | null;
  status: 'active' | 'inactive' | 'building' | 'error';
  created_at: string;
  updated_at: string;
}

// Simple task type for Kanban display
interface Task {
  id: string;
  title: string;
  description: string;
  status: 'backlog' | 'in_progress' | 'ai_review' | 'human_review' | 'done';
  createdAt: string;
}

interface ProjectDetailPageProps {
  project: Project;
  onBack: () => void;
}

// Kanban column component
function KanbanColumn({
  title,
  count,
  tasks,
  color,
  emptyIcon: EmptyIcon,
  emptyMessage,
  emptySubtext,
  onAddClick,
}: {
  title: string;
  count: number;
  tasks: Task[];
  color: string;
  emptyIcon: React.ElementType;
  emptyMessage: string;
  emptySubtext?: string;
  onAddClick?: () => void;
}) {
  return (
    <div className="flex-1 min-w-[280px] max-w-[320px]">
      <div className={`h-1 rounded-t-lg ${color}`} />
      <div className="bg-card/50 border border-border border-t-0 rounded-b-lg">
        <div className="p-3 border-b border-border flex items-center justify-between">
          <div className="flex items-center gap-2">
            <span className="font-medium text-foreground text-sm">{title}</span>
            <span className="text-xs text-muted-foreground bg-secondary px-1.5 py-0.5 rounded">
              {count}
            </span>
          </div>
          {onAddClick && (
            <Button variant="ghost" size="icon" className="h-6 w-6" onClick={onAddClick}>
              <Plus className="h-4 w-4" />
            </Button>
          )}
        </div>
        <ScrollArea className="h-[calc(100vh-280px)]">
          <div className="p-2 space-y-2">
            {tasks.length === 0 ? (
              <div className="flex flex-col items-center justify-center py-8 text-center">
                <EmptyIcon className="h-8 w-8 text-muted-foreground/30 mb-2" />
                <p className="text-sm text-muted-foreground">{emptyMessage}</p>
                {emptySubtext && (
                  <p className="text-xs text-muted-foreground/70 mt-1">{emptySubtext}</p>
                )}
              </div>
            ) : (
              tasks.map((task) => (
                <div
                  key={task.id}
                  className="p-3 bg-card border border-border rounded-lg hover:border-primary/50 cursor-pointer transition-colors"
                >
                  <h4 className="text-sm font-medium text-foreground mb-1">{task.title}</h4>
                  <p className="text-xs text-muted-foreground line-clamp-2">{task.description}</p>
                </div>
              ))
            )}
          </div>
        </ScrollArea>
      </div>
    </div>
  );
}

